@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Work Sans', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background-color:#0a0e11;height:100vh;overflow:hidden}.App-header{display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:2em;color:white;height:40%}.App-logo-container{width:0;height:0;display:flex;justify-content:center;align-items:center;border-radius:50%;box-shadow:0px 0px 20px 20px #ffffff;-webkit-animation:glow 1s linear infinite alternate;animation:glow 1s linear infinite alternate;margin:75px}.App-logo{width:100px;height:100px}.App-main{display:flex;width:100%;align-items:center;justify-content:center}.App-footer button{width:140px;height:6vh;font-size:1.2em;text-transform:uppercase;letter-spacing:2.5px;font-weight:500;color:#000;background-color:#d6e0f0;border:none;border-radius:45px;box-shadow:0px 8px 15px rgba(0,0,0,0.1);transition:all 0.3s ease 0s;cursor:pointer;outline:none;margin:2vh 10px}.App-footer button:hover{background-color:#d6e0f0 !important;box-shadow:0px 5px 8px #d6e0f0 !important;color:black !important;transform:translateY(-7px)}.App-footer button:active{transform:translateY(-3px)}.game{display:table;border-spacing:20px}.row{display:table-row}.cell{width:100px;height:100px;background:#eee;border-radius:10%;display:table-cell;box-shadow:-10px -10px 30px 4px rgba(0,0,0,0.1),10px 10px 30px 4px rgba(45,78,255,0.15);transition:all 0.3s ease 0s;cursor:pointer}.cell:hover{color:black !important;transform:translateY(-7px)}@-webkit-keyframes glow{to{box-shadow:0px 0px 30px 20px #fff9e8}}@keyframes glow{to{box-shadow:0px 0px 30px 20px #fff9e8}}

